import { parseProjectPath, projectHref, isProjectSectionActive } from './paths';

describe('parseProjectPath', () => {
  test.each([
    ['/projects/123/flashcards',        { base:'projects', projectId:'123', section:'flashcards' }],
    ['/active-project/123/flashcards/', { base:'active-project', projectId:'123', section:'flashcards' }],
    ['/en/projects/123/diagnostics',    { base:'projects', projectId:'123', section:'diagnostics' }],
    ['/en-US/active-project/123/files', { base:'active-project', projectId:'123', section:'files' }],
    ['/projects/123',                   { base:'projects', projectId:'123', section:undefined }],
    ['/active-project/123/',            { base:'active-project', projectId:'123', section:undefined }],
    ['/projects/123/flashcards/edit/23', { base:'projects', projectId:'123', section:'flashcards' }],
    ['/active-project/123/flashcards/edit/23/', { base:'active-project', projectId:'123', section:'flashcards' }],
    ['/projects/123?param=value',       { base:'projects', projectId:'123', section:undefined }],
    ['/active-project/123#section',     { base:'active-project', projectId:'123', section:undefined }],
    ['/other/123/flashcards',           {}],
    ['/projects',                        {}],
    ['/active-project',                 {}],
    ['/',                               {}],
    ['',                                {}],
  ])('parse %s', (pathname, expected) => {
    expect(parseProjectPath(pathname)).toMatchObject(expected);
  });
});

describe('projectHref', () => {
  test('generates correct URLs', () => {
    expect(projectHref('123', 'flashcards')).toBe('/active-project/123/flashcards');
    expect(projectHref('123', 'flashcards', 'edit', 23)).toBe('/active-project/123/flashcards/edit/23');
    expect(projectHref('123')).toBe('/active-project/123');
    expect(projectHref('123', '')).toBe('/active-project/123');
  });

  test('handles special characters in projectId', () => {
    expect(projectHref('123-456', 'flashcards')).toBe('/active-project/123-456/flashcards');
    expect(projectHref('123/456', 'flashcards')).toBe('/active-project/123%2F456/flashcards');
  });
});

describe('isProjectSectionActive', () => {
  test('correctly identifies active sections', () => {
    expect(isProjectSectionActive('/active-project/123/flashcards', '123', 'flashcards')).toBe(true);
    expect(isProjectSectionActive('/projects/123/flashcards', '123', 'flashcards')).toBe(true);
    expect(isProjectSectionActive('/active-project/123/flashcards/edit/23', '123', 'flashcards')).toBe(true);
    expect(isProjectSectionActive('/active-project/123/files', '123', 'flashcards')).toBe(false);
    expect(isProjectSectionActive('/active-project/456/flashcards', '123', 'flashcards')).toBe(false);
  });
});

